.popularItem {
  box-sizing: border-box;
  width: 100%;
  padding: 12px;
  background-color: #fff;
  .content-avatar {
    text-align: center;
    overflow: hidden;
    margin-bottom: 20px;
    img {
      height: 118px;
    }
  }
  .score {
    text-align: center;
    margin: 20px 0;
  }
  i {
    margin-right: 10px;
  }
  .error-lists {
    display: flex;
    flex-direction: column;
    .error-lists .errors + .errors {
      margin-top: 20px;
    }
  }
}
